const SESSION_HIS_KEY = ''; // 缓存历史会话KEY
const API_KEY = 'OPENROUTER_API_KEY'; // 缓存数据API KEY
const API_URL = 'https://openrouter.ai/api/v1/chat/completions'; // ai 访问api openrouter.ai 接口
const API_MODEL = 'mistralai/mistral-7b-instruct:free'; // openrouter.ai 访问方式

export const saveKey = function (value) {
  localStorage.setItem(API_KEY, value);
}

export const saveSession = function (data) {
  sessionStorage.setItem(SESSION_HIS_KEY, data);
}

export const getAI = async function (messages) {
  const YOUR_SITE_URL = '';
  const YOUR_SITE_NAME = '';
  const OPENROUTER_API_KEY = localStorage.getItem(API_KEY);
  const response = await fetch(`${API_URL}`, {
    method: "POST",
    headers: {
      "Authorization": `Bearer ${OPENROUTER_API_KEY}`,
      "HTTP-Referer": `${YOUR_SITE_URL}`,
      "X-Title": `${YOUR_SITE_NAME}`,
      "Content-Type": "application/json"
    },
    body: JSON.stringify({
      "model": `${API_MODEL}`, // Optional (user controls the default),
      "messages": messages,
    })
  });
  const result = await response.json();
  return result;
}