import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import ListItemAvatar from '@material-ui/core/ListItemAvatar';
import Avatar from '@material-ui/core/Avatar';
import FaceIcon from '@material-ui/icons/Face';

const useStyles = makeStyles((theme) => ({
  root: {
    width: '100%',
    backgroundColor: theme.palette.background.paper,
  },
  inline: {
    display: 'inline',
  },
  avatarMe: {
    backgroundColor: '#ffbf00',
    width: theme.spacing(4),
    height: theme.spacing(4),
  },
  avatarAi: {
    backgroundColor: '#03cc7f',
    width: theme.spacing(4),
    height: theme.spacing(4),
  },
}));

export default function WrapList ({ data }) {
  const classes = useStyles();
  return (
    <List id="listRef" className={classes.root}>
      {
        data.map((item, index) => {
          return (
            <ListItem alignItems="flex-start" key={index}>
              <ListItemAvatar>
                <Avatar className={item.role === 'user' ? classes.avatarMe : classes.avatarAi}>
                  <FaceIcon />
                </Avatar>
              </ListItemAvatar>
              <ListItemText
                primary={item.role === 'user' ? 'You' : 'AI'}
                secondary={
                  <React.Fragment>
                    {
                      item.role !== 'error' ? item.content : <p style={{color: 'red'}}>{item.content}</p>
                    }
                  </React.Fragment>
                }
              />
            </ListItem>
          )
        })
      }
    </List>
  );
}
