import React from 'react';
import WrapList from './element/list'
import WrapSend from './element/send'
import Container from '@material-ui/core/Container';
import './App.css';

function App() {
  const classes = {
    root: 'Container-root'
  };
  const [list, setList] = React.useState([]);
  const update = (data)=>{
    setList(data);
  }
  return (
    <Container maxWidth="sm" classes={classes}>
      <WrapList data={list} />
      <WrapSend data={list} onUpdate={update}/>
    </Container>
  );
}

export default App;
