import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import InputBase from '@material-ui/core/InputBase';
import IconButton from '@material-ui/core/IconButton';
import ArrowUpwardIcon from '@material-ui/icons/ArrowUpward';
import SettingsIcon from '@material-ui/icons/Settings';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import CircularProgress from '@material-ui/core/CircularProgress';
import Modal from '@material-ui/core/Modal';
import { green } from '@material-ui/core/colors';
import { getAI, saveKey } from '../lib';

const useStyles = makeStyles((theme) => ({
  root: {
    padding: '2px 8px',
    display: 'flex',
    alignItems: 'center',
    borderRadius: '16px',
    position: 'relative',
  },
  input: {
    marginLeft: theme.spacing(1),
    flex: 1,
  },
  iconButton: {
    padding: 10,
  },
  progress: {
    color: green[500],
    position: 'absolute',
    top: '50%',
    left: '50%',
    marginTop: -12,
    marginLeft: -12,
  },
}));

export default function WrapSend ({ data, onUpdate }) {
  const classes = useStyles();
  const [open, setOpen] = React.useState(false);
  const [apiKey, setAPIKey] = React.useState('');
  const [loading, setLoading] = React.useState(false);

  const handleUpload = () => {
    console.log('上传附件功能暂不支持');
  }

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const handleSave = () => {
    setOpen(false);
    saveKey(apiKey);
    setAPIKey('');
  }

  const handleInput = (e) => {
    setAPIKey(e.target.value);
  }

  const scrollBottom = () => {
    setTimeout(()=>{
      document.querySelector('#listRef>li:last-child').scrollIntoView({ behavior: "smooth", block: "end", inline: "nearest" });
    },300)
  }

  const handleEnter = async (e) => {
    if (e.code === 'Enter') {
      const msg = e.target.value;
      data.push({
        role: "user",
        content: msg
      })
      e.target.value = '';
      onUpdate(data.slice());
      setLoading(true);
      scrollBottom();
      const result = await getAI(data);
      if (result.choices && result.choices[0]) {
        data.push(result.choices[0].message);
      } else {
        data.push({
          role: 'error',
          content: result.error.message
        });
      }
      onUpdate(data.slice());
      setLoading(false);
      scrollBottom();
    }
  }

  return (
    <div>
      <Paper component="form" disabled={loading} className={classes.root} variant="outlined">
        <InputBase
          className={classes.input}
          placeholder="输入你想说的话，Enter"
          inputProps={{ 'aria-label': 'Message' }}
          onKeyDown={handleEnter}
        />
        <input hidden/>  
        <IconButton onClick={handleUpload} type="button" className={classes.iconButton} aria-label="upload">
          <ArrowUpwardIcon />
        </IconButton>
        <IconButton onClick={handleClickOpen} type="button" className={classes.iconButton} aria-label="setting">
          <SettingsIcon />
        </IconButton>
        {loading && <CircularProgress size={24} className={classes.progress} />}
      </Paper>
      <Dialog id="settingDialog" open={open} onClose={handleClose} aria-labelledby="form-dialog-title">
        <DialogTitle id="form-dialog-title">设置OpenAI API key</DialogTitle>
        <DialogContent>
          <TextField
            autoFocus
            margin="dense"
            id="key"
            label="Please enter the key"
            type="text"
            fullWidth
            value={apiKey}
            onChange={handleInput}
          />
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose} color="primary">
            取消
          </Button>
          <Button onClick={handleSave} color="primary">
            设置
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}
